import { Link } from "gatsby"
import React from "react"
import styles from "./footer.module.scss"


import BandukaLogo from "../../images/Dr Banduka_logo beli.svg"
import facebook from "../../images/ikona FB.svg"
import instagram from "../../images/ikona instagram.svg"

import lokacija from "../../images/Ikona adresa_bela.svg"
import telefon from "../../images/Ikona telefon_bela.svg"
import radno from "../../images/Ikona radno vreme_bela.svg"
import zakazivanje from "../../images/Ikona zakazi pregled_bela.svg"


const Footer = ({ siteTitle }) => (
 <footer>
     <div className={styles.footer_wraper}>
         <div className={styles.footer_wraper_top}>
             <div className={styles.fotter_inner, styles.logo}>
                 <Link to="/">
                     <img src={BandukaLogo} alt="Dr Banduka logo"></img>
                 </Link>
             </div>
             <div className={styles.fotter_inner}>
                 <p>BROJ LICENCE: 301739 <br/> PIB: 105661211</p>
             </div>
             <div className={styles.fotter_inner}></div>
             <div className={styles.fotter_inner}>
                 <div className={styles.text}><p>pratite nas</p></div>
                 <div className={styles.social}>
                     <Link to="/">
                         <img src={facebook} alt="Dr Banduka facebook" />
                     </Link>
                 </div>
                 <div className={styles.social}>
                     <Link to="/">
                         <img src={instagram} alt="Dr Banduka instagram" />
                     </Link>
                 </div>
             </div>
         </div>
         <div className={styles.footer_wraper_bottom}>
             <div className={styles.fotter_inner}>
                 <div className={styles.fotter_inner_img}>
                     <img src={lokacija} alt="Dr Banduka stomatoloska ordinacija img" />
                 </div>
                 <div className={styles.fotter_iner_text}>
                     <p>Mihaila Todorovića 14b, <br/> Zvezdara, Beograd </p>
                 </div>
             </div>
             <div className={styles.fotter_inner}>
                 <div className={styles.fotter_inner_img}>
                     <img src={telefon} alt="Dr Banduka stomatoloska ordinacija img" />
                 </div>
                 <div className={styles.fotter_iner_text}>
                     <p>011 123 45 66 <br/> 064 17 39 442 </p>
                 </div>
             </div>
             <div className={styles.fotter_inner}>
                 <div className={styles.fotter_inner_img}>
                     <img src={radno} alt="Dr Banduka stomatoloska ordinacija img" />
                 </div>
                 <div className={styles.fotter_iner_text}>
                     <p>RADNO VREME <br/>PON-SUB 09.00-18.00 </p>
                 </div>
             </div>
             <div className={styles.fotter_inner}>
                 <div className={styles.fotter_inner_img}>
                     <img src={zakazivanje} alt="Dr Banduka stomatoloska ordinacija img" />
                 </div>
                 <Link to="/" className={styles.fotter_iner_text}>
                 <div className={styles.fotter_inner_textP}>
                     <p><strong>ZAKAŽI PREGLED</strong> <br/> bandukab@beotel.rs </p>
                 </div>
                 </Link>
             </div>
         </div>
     </div>
     <div className={styles.copyright}>
         <p>© Copyright - <strong>Stomatološka ordinacija Dr Banduka</strong></p>
     </div>
     <div className={styles.bottom_gray}></div>
 </footer>
)


export default Footer
