import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import headerStyles from "./header.module.scss"

import BandukaLogo from "../../images/Dr Banduka_logo color.svg"
import lokacija from "../../images/Ikona adresa.svg"
import telefon from "../../images/Ikona telefon.svg"
import radno from "../../images/Ikona radno vreme.svg"
import zakazivanje from "../../images/Ikona zakazi pregled.svg"

const Header = ({ siteTitle }) => (
  <header className={headerStyles.Header}>
    <div className={headerStyles.top}></div>
    <div className={headerStyles.holder}>
      <div className={headerStyles.logo}>
      <Link to="/">
        <img src={BandukaLogo} alt="Dr Banduka logo"/>
      </Link>
        
      </div>
      <nav>
        <ul>
          <li><Link to="/">Početna</Link></li>
          <li><Link to="/oNama">O nama</Link></li>
          <li><Link to="/usluge">Usluge</Link></li>
          <li><Link to="/galerija">Galerija</Link></li>
          <li><Link to="/pitanja">Česta pitanja</Link></li>
          <li><Link to="/akcije">Akcije</Link></li>
          <li><Link to="/kontakt">Kontakt</Link></li>
        </ul>
      </nav>
    </div>
    <div className={headerStyles.header_bottom_wraper}>
      <div className={headerStyles.header_bottom}>
        <div className={headerStyles.header_bottom_inner}>
          <div className={headerStyles.bottom_icon}>
            <img src={lokacija} alt="Dr Banduka stomatoloska ordinacija"></img>
          </div>
          <div className={headerStyles.bottom_right}>
            <div className={headerStyles.bottom_right_text}><p>Mihaila Todorovića 14b,</p></div>
            <div className={headerStyles.bottom_right_text}><p>Zvezdara, Beograd</p></div>
          </div>
        </div>
        <div className={headerStyles.header_bottom_inner}>
          <div className={headerStyles.bottom_icon}>
            <img src={telefon} alt="Dr Banduka stomatoloska ordinacija"></img>
          </div>
          <div className={headerStyles.bottom_right}>
            <div className={headerStyles.bottom_right_text}><p>011 123 45 66</p></div>
            <div className={headerStyles.bottom_right_text}><p>064 17 39 442</p></div>
          </div>
        </div>
        <div className={headerStyles.header_bottom_inner}>
          <div className={headerStyles.bottom_icon}>
            <img src={radno} alt="Dr Banduka stomatoloska ordinacija"></img>
          </div>
          <div className={headerStyles.bottom_right}>
            <div className={headerStyles.bottom_right_text}><p>RADNO VREME</p></div>
            <div className={headerStyles.bottom_right_text}><p>PON - SUB 09:00 - 18:00</p></div>
          </div>
        </div>
        <div className={headerStyles.header_bottom_inner}>
          <div className={headerStyles.bottom_icon}>
            <img src={zakazivanje} alt="Dr Banduka stomatoloska ordinacija"></img>
          </div>
          
          <Link to="/kontakt">
            <div className={headerStyles.bottom_right}>
              <div className={headerStyles.bottom_right_text}><strong><p>ZAKAŽI PREGLED</p></strong></div>
              <div className={headerStyles.bottom_right_text}><p>bandukab@beotel.rs</p></div>
            </div>
          </Link>
        </div>
      </div>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
